<template>
  <div>

    <b-carousel
      id="carousel-no-animation"
      style="text-shadow: 0px 0px 2px #000"
      no-animation
      indicators
      img-width="1024"
      img-height="480">

      <div
        v-for="(banner, index) in banner_data"
        :key="banner.id">
        <b-carousel-slide
          :caption="banner.caption"
          :img-src="banner.img" />
      </div>

    </b-carousel>

  </div>
</template>

<script>

const imgSample = 'https://firebasestorage.googleapis.com/v0/b/vuejs-firebase-02-98e6a.appspot.com/o/New%20Project%20(8).png?alt=media&token=c53aa2aa-4bcc-4d04-bebe-510b1ba1a250';

export default {
  name: 'jBanner',
  data() {
    return {
      banner_data: [
        {
          id: 2,
          caption: '',
          img: imgSample,
        }
      ],
    };
  },
};
</script>



