/* eslint-disable no-shadow,consistent-return,no-param-reassign */
// import axios from 'axios';
import apiProduct from '@/services/jApiProduct';
// import Vue from 'vue';

const state = {
  // Set default state(s) here
  state_product: {
    product_list: null,
  },
};

const getters = {
  // Change value of state(s) with getter

};

const mutations = {

  setProductList(state, product) {
    state.state_product.product_list = product.data;
  },
};

const actions = {

  async getProductList({ commit }) {
    try {
      const listProduct = await apiProduct.getProductList();
      commit('setProductList', listProduct);
    } catch (err) {
      throw err;
    }
  },

};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



