<template>
  <div class="jProduct">
    <b-row>

      <div
        v-for="product in sort_product_data"
        :key="product.id">
        <b-col>
          <b-card
            :title="product.name"
            img-src="https://picsum.photos/600/300/?image=25"
            img-alt="Image"
            img-top
            tag="article"
            style="max-width: 250px;"
            class="mb-2 pointer">

            <div>
              Rp. {{ product.amounts[0].price }}
            </div>

            <div>
              <i class="fa fa-shopping-cart"/>
              Tipe: Eceran, Grosir
            </div>

            <div>
              <i class="fa fa-expand"/>
              Ukuran: -
            </div>

            <div>
              <i class="fa fa-check-square"/>
              Kondisi: {{ product.conditions.join().replace(',', ', ') }}
            </div>

            <div>
              <i class="fa fa-archive"/>
              Minimal Order: {{ product.minimum_order }} {{ product.unit_measure }}
            </div>

            <div>
              <i class="fa fa-check"/>
              Ketersediaan: {{ product.stock }}
            </div>

            <br>

            <b-button
              href="#"
              class="b--blue basket-btn">
              Tambahkan di Keranjang
            </b-button>
          </b-card>
        </b-col>
      </div>

    </b-row>

    <br>

    <div class="float-to-center show-more">
      <b-button> Show more </b-button>
    </div>

  </div>
</template>

<script>
import { mapActions } from 'vuex';

const dataJSON = [
  {
    id: 2,
    name: 'Gurame',
    minimum_order: 1,
    unit_measure: "kg",
    conditions: ['Fresh', 'Frozen'],
    stock: 20,
    image: 'fotoikan',
    created_date: '1999-01-0123: 01: 01',
    updated_date: '1999-01-0123: 01: 01',
    amounts: [
      {
        price: 10000,
        limit_minimum: 21,
        limit_maximum: 40,
      },
      {
        price: 20000,
        limit_minimum: 1,
        limit_maximum: 20,
      },
    ],
  },
  {
    id: 3,
    name: 'Tuna',
    minimum_order: 1,
    unit_measure: "kg",
    conditions: ["Fresh"],
    stock: 20,
    image: 'fotoikan',
    created_date: '1999-01-0123: 01: 01',
    updated_date: '1999-01-0123: 01: 01',
    amounts: [
      {
        price: 10000,
        limit_minimum: 21,
        limit_maximum: 40,
      },
      {
        price: 20000,
        limit_minimum: 1,
        limit_maximum: 20,
      },
    ],
  },
];

export default {
  name: 'jProduct',
  data() {
    return {
      product_data: [
        ...dataJSON,
      ],
    };
  },
  computed: {
    sort_product_data() {
      const sortAmount = this.filterPrice(this.product_data);
      return sortAmount;
    }
  },
  created() {
    // console.log('apa iya');
    // this.getProductList();
  },
  methods: {
    // ...mapActions('jStoreProduct', ['getProductList']),

    filterPrice(price) {
      const priceModif = price;
      priceModif.forEach((val, index) => {
        val.amounts.sort((a, b) => b.price - a.price);
      });
      return priceModif;
    },
  },
};
</script>



